//React
import React from 'react';
import {Link} from 'react-router-dom';

class Home extends React.Component {
    constructor (props) {
        super();
    }
    
    render(){
        return(
          <div className="container">
            <h1>Westerwyn Hollow</h1>
            <Link to={{pathname: '/eddie'}}>Eddie the Lizard</Link>
          </div>
        )
    }
}

export default Home;



